import { jsx } from "@vertx/jsx";
import * as Tone from "tone";
import { EffectsStackView } from "../views/patch/effectsStackView";
import { Utils } from "./Utils";

// Currently only works on player Nodes
export class EffectsStack {

    constructor(channel,contextHTML,destination,color) {
        this.channel = channel;
        this.context = contextHTML;
        this.destination = destination
        this.color = color
    }

    stack:Tone.InputNode[] = [];
    channel:Tone.Channel;
    destination:Tone.Channel;
    context:HTMLElement

    reverb:Tone.Reverb;
    bitCrush:Tone.BitCrusher;
    color:string;


    render() {
        let effectsStack = <EffectsStackView channel={this.channel} destination={this.destination} context={this.context} color={Utils.getColorValue(this.color)} name="Effects"></EffectsStackView>
        this.context.appendChild(effectsStack);
    }


}