import { jsx, JSXComponent, JSXElement } from "@vertx/jsx";

export class PatchItem implements JSXComponent {

    name:string | undefined;

    render(children:JSXElement[]):JSXElement {

        let patchItem = <div class="d-flex align-center mr-2">
                            {children}
                            <p class="text-align-center">{this.name}</p>
                        </div> as HTMLElement

        return patchItem;
    }
}

