// External Types
export type JSXElement = HTMLElement | SVGElement | Text;
export type JSXChild = JSXElement | JSXComponent;

export interface JSXComponent {
    render(children: JSXElement[]): JSXElement
}

// Internal Types
/**
 * Used to prefix logs from this package.
 */
const packageName: string = "[@vertx/jsx]";
type JSXTagOrComponentClass = string | JSXComponentConstructor;
interface JSXComponentConstructor {
    new(): JSXComponent
}


// Configure the JSX interface for TypeScript intellisense.
declare global {
    namespace JSX {
        // JSX.IntrinsicElements specifies a dictionary of usable lowercase JSX tags.
        interface IntrinsicElements {
            [tagName: string]: any
        }

        // JSX.Element specifies the return type of lowercase JSX tags.
        type Element = JSXElement;
    }
}

export class JSXError extends Error {
    inner?: Error;
    tag?: string;
    props?: any;
    children?: any[];

    constructor(message: string, inner?: Error, tag?: string, props?: any, children?: any[]) {
        super(message);
        this.name = `[${packageName}] JSXError`;
        this.inner = inner;
        this.tag = tag;
        this.props = props;
        this.children = children;
    }
}

export function jsx(tag: JSXTagOrComponentClass, props: any, ...children: JSXChild[]): JSXElement {
    // super basic implementation. no model binding etc.

    // if any child was an instantiated JSXElement, invoke the render method instead.
    // otherwise, pass through the HTML Element.
    const flatChildren = children
        .reduce((acc, val) => acc.concat(val), [] as JSXChild[]);

    const htmlSvgChildren = flatChildren.map(childToHtmlSvgElement);

    // if rendering a plain/intrinsic element, such as <div>, output the HTML
    if (typeof tag === 'string')
        return createJsxIntrinsic(tag, props, htmlSvgChildren);

    // otherwise we're rendering a component, render it to output the HTML.
    const comp: JSXComponent = Object.assign(new tag(), props);
    return comp.render(htmlSvgChildren);
}

function createJsxIntrinsic(tagName: string, props: any, children: JSXElement[]): JSXElement {
    try {
        props = props || {};

        const ele = document.createElement(tagName);

        // implementer note: don't be tempted to use 'object.assign' here, 
        // some html attributes differ from the javascript property name!
        // eg. class in html == className in js.
        // todo: benchmark the fastest way to do this.
        Object.getOwnPropertyNames(props)
            .forEach((propName) => {
                // special case for events: setAttribute here will not work correctly.
                if (propName.startsWith('on')) {
                    ele.addEventListener(propName.substr(2), (props[propName]).bind(null));
                    return;
                }

                // everything else generally can be set as a string
                ele.setAttribute(propName, props[propName])
            });

        children.forEach(c => Array.isArray(c) ? ele.append(...c) : ele.append(c));

        return ele;
    }
    catch (e) {
        console.error(`[@vertex/ui] JSXError Details: `, { tagName, properties: props, children, innerError: e });
        throw new JSXError(`Failed to create HTML or SVG (Intrinsic) element.
The name '${tagName}' may not be a valid HTML or SVG tag, or one or more of the properties may be invalid.
See the developer console for more details.`, e, tagName, props, children);
    }
}

/**
 * if the child is a renderable component, returns the render. otherwise, passes through the child.
 * @param child 
 */
function childToHtmlSvgElement(child: JSXChild): JSXElement {
    if (child instanceof Element || typeof child === 'string')
        return child;

    if ('render' in child)
        return child.render([]);

    throw new JSXError("Unsupported Child in JSX element", null, null, null, [child]);
}
