import { User, UserManager } from "oidc-client";

/**
 * @returns a default IVertexAuthOptions:
 *   using vertx.cloud as the stack, and the openid, profile, api_vertex_core scopes.
 */
export function getDefaultOptions(): IVertexAuthOptions {
    return {
        vertxStackHost: "vertx.cloud",
        scopes: ["openid", "profile", "api_vertex_core"]
    }
}

export class VertexAuth implements ICredentialProvider {

    private _initialized: boolean = false;
    private _userManager: UserManager;
    private _stackHost: string = "vertx.cloud";

    /**
     * configures the auth library and handles any pending authentication tasks.
     * callers should await this method.
     */
    public async initAsync(options?: IVertexAuthOptions): Promise<void> {
        const config = Object.assign(getDefaultOptions(), options);

        this._stackHost = config.vertxStackHost!;

        this._userManager = new UserManager({
            client_id: config.clientId,
            authority: this.IdentityServerUrl,
            redirect_uri: config.redirectUri,
            post_logout_redirect_uri: config.redirectUri,
            response_type: "code",
            scope: (config.scopes?.slice() || []).join(' '),
            // disable session monitoring, since it doesnt work with third-party 
            // cookies being blocked, as in pretty much all browsers now
            automaticSilentRenew: false,
            monitorSession: false,
            revokeAccessTokenOnSignout: true,
        });

        // perform session maintenance
        this._userManager.clearStaleState();

        // check for and handle callbacks
        if (this._shouldHandleCallback()) {
            console.log("[VERTX Auth] Handling PKCE Code from URL...");
            try {
                await this._userManager.signinRedirectCallback();
                console.log("[VERTX Auth] Successfully completed PKCE sign in");
            }
            catch (e) {
                const params = location.search;
                console.warn("[VERTX Auth] PKCE Auth Failed.", {
                    urlParams: params,
                    error: e,
                });
            }

            // clear the params from the url
            this._clearOauthUrlParams();
        }

        // set initialized
        this._initialized = true;
    }

    /**
     * returns the identity server URL for the current VERTX stack, without the trailing slash
     * @example https://vertx.cloud/identity
     */
    public get IdentityServerUrl(): string {
        return `https://${this._stackHost}/identity`
    }

    /**
     * returns the VERTX Base URL for the current VERTX stack, without the trailing slash
     * @example https://vertx.cloud
     */
    public get VertexBase(): string {
        return `https://${this._stackHost}`;
    }

    /**
     * get an access token for the specified URL.
     * this function can be used to ensure that access tokens are not given 
     * to external/third-party domains by accident.
     * @param url the url you are planning to make a request to.
     */
    public async getToken(url: string): Promise<string | null> {
        this._assertInitialized("getToken");
        try {
            if (url.startsWith(this.VertexBase)) {
                let user = await this.getUserAsync();
                return user?.access_token || null;
            }
            return null;
        }
        catch {
            return null;
        }
    }


    /**
     * get the underlying UserManager object from oidc-client.
     * this is only recommended for advanced scenarios.
     * 
     * note that the underlying implementation could change in the future, so
     * it is recommended not to directly use userManager.
     * @returns the userManager instance
     */
    public getUserManager(): UserManager {
        this._assertInitialized("getUserManager");
        return this._userManager;
    }

    /**
     * checks if the user has a valid token, and returns true if they do.
     * if not, the user will immediately be redirected to sign in.
     * 
     * use of this method is generally not recommended, since it results
     * in a poor user experience. it's recommended to check the session
     * yourself with 'isUserExpired' and only sign in once you have informed
     * the user.
     */
    public async ensureSignedInAsync(): Promise<boolean> {
        this._assertInitialized("ensureSignedInAsync");

        let user = await this._userManager.getUser();

        if (!this.isUserExpired(user))
            return true;

        // todo: insert state to redirect back to right place
        this._userManager.signinRedirect({
            state: this.createSigninState()
        });

        return false;
    }

    /**
     * redirects to the sign in page.
     * @param options sign in options.
     */
    public redirectToSignIn(options?: SignInOptions) {
        options = Object.assign({}, options || {});

        this._assertInitialized("redirectToSignIn");

        this._userManager.signinRedirect({
            state: this.createSigninState(),
            prompt: this._promptModeForOption(options.mode)
        });

        return new Promise((_) => {
            // return an empty promise to allow the caller to wait for redirect if they await
        });
    }

    public async redirectToSignOut(options?: SignOutOptions) {
        options = Object.assign({}, options || {});

        this._assertInitialized("redirectToSignOut");

        const user = await this._userManager.getUser();
        if (options.signoutVertex) {
            const idhint = user && user.id_token;

            await this._userManager.signoutRedirect({
                state: this.createSigninState(),
                id_token_hint: idhint
            });

            return new Promise((_) => {
                // empty promise to allow the caller to await redirect
            });
        }
        else {
            await this._userManager.revokeAccessToken();
            await this._userManager.removeUser();
        }
    }

    /**
     * begins watching the user session status in the background. 
     * invokes 'callback' when the session has expired. this can be used to 
     * inform the user that they will need to log in again soon.
     * @param callback optional. callback to fire when session expires. if null, the page will immediately redirect to sign in on session expiry.
     */
    public watchSession = (callback?: SessionExpiryCallback) => {
        this._assertInitialized("watchSession");

        if (typeof callback !== 'function')
            callback = this._defaultSessionExpiredCallback;

        let self = this;
        setInterval(async () => {
            console.log("[VERTX:Auth] Checking session state...");
            let user = await self._userManager.getUser();
            if (this.isUserExpired(user)) {
                callback(this, user);
                return;
            }
        }, 10 * 1000);
    }

    /**
     * 
     * @returns user info if signed in, otherwise returns null
     */
    public getUserAsync(): Promise<User | null> {
        this._assertInitialized("getUserAsync");
        return this._userManager.getUser();
    }

    private isUserExpired(user: User | null): boolean {
        // todo: configure expiry time
        return !user || user.expired || user.expires_in < 300;
    }

    private _promptModeForOption(promptOption?: "normal" | "prompt" | "silent"): "none" | "login" | undefined {
        if (!promptOption || promptOption === "normal")
            return undefined;

        if (promptOption === "prompt")
            return "login";

        if (promptOption === "silent")
            return "none";
    }

    private createSigninState(): OAuthState {
        // todo: provide a mechanism to allow library users to insert
        //       more state variables.

        let appUri = `${location.pathname}${location.search}${location.hash}`;

        let stateObj: AuthorizeState = {
            appUri: appUri
        };

        return JSON.stringify(stateObj);
    }

    private processSigninState(state: OAuthState) {
        try {
            // todo: provide a mechanism to allow library users to 
            //       handle custom state.
            // todo: should hash this or something to prevent open redirects
            let obj = JSON.parse(state) as AuthorizeState;
            if (!obj || !obj.appUri || typeof obj.appUri !== 'string')
                throw new Error("Invalid State");

            if (obj.appUri.startsWith('/') === false)
                throw new Error("Invalid State");

            location.href = obj.appUri;
        }
        catch {
            throw new Error("Invalid State");
        }
    }

    // implementation note: this must be an arrow function to keep the correct 'this' context.
    private _defaultSessionExpiredCallback = () => {
        console.warn("[VERTX:Auth] Session expired, invoking silent sign in.\n" +
            "You can override this behaviour by passing a callback to 'watchSession'");

        this.ensureSignedInAsync();
    }


    private _assertInitialized(callerName: string) {
        if (!this._initialized) {
            throw new VertexAuthError(`${callerName} was called before VertexAuth was initialized.
await initAsync before using any other methods on VertexAuth.`);
        }
    }

    private _shouldHandleCallback() {
        const hasState = location.search.indexOf("state=") !== -1;
        const hasCode = location.search.indexOf("code=") !== -1;
        const hasError = location.search.indexOf("error=") !== -1;

        return hasState && (hasCode || hasError);
    }

    private _clearOauthUrlParams() {
        const params = new URLSearchParams(location.search);
        params.delete("code");
        params.delete("scope");
        params.delete("state");
        params.delete("session_state");
        params.delete("error");

        const newsearch = params.toString();

        const newUrl = `${location.pathname}${newsearch ? `?` + newsearch : ''}${location.hash}`;

        history.pushState(null, document.title, newUrl);
    }
}

export class VertexAuthError extends Error {
    constructor(message?: string) {
        super(`[@vertx/auth] ${message}`);
    }
}

// ==== types

export interface ICredentialProvider {
    /**
     * get an access token for the specified URL.
     * this function can be used to ensure that access tokens are not given 
     * to external/third-party domains by accident.
     * @param url the url you are planning to make a request to.
     */
    getToken(url: string): Promise<string | null>;
}

export interface IVertexAuthOptions {
    /** 
     * (optional) hostname of the VERTX stack.
     * do not specify the https protocol, or any trailing slashes.
     * defaults to vertx.cloud
     * @example vertx.cloud
     * */
    vertxStackHost?: string

    /**
     * (optional) the Client ID (App ID) of your VERTX App.
     * This parameter is optional, but it must be provided before attempting to authenticate.
     */
    clientId?: string

    /**
     * (optional) the URI to redirect back to after a user completes a sign in.
     * in most cases, this uri must be registered with the client ID.
     * This parameter is optional, but it must be provided before attempting to authenticate.
     */
    redirectUri?: string

    /**
     * (optional) the list of APIs and Identity info your app will need to access.
     * This parameter will override any previously configured scopes with the newly provided value.
     * @see addScopes to configure additional scopes without overwriting any already configured.
     */
    scopes?: string[]

    /**
     * (optional) a list of APIs and Identity info your app will need to access.
     * This parameter adds more scopes without erasing any default/previously configured scopes.
     * @see scopes to exactly specify the list of scopes to use, removing any defaults or previously configured ones.
     */
    //addScopes?: string[]
}

export interface SessionExpiryCallback {
    (this: any, auth: VertexAuth, user?: User | null): void;
}

export interface AuthorizeState {
    appUri: string
}

export type OAuthState = string;

export interface SignInOptions {
    /**
     * the sign in mode to use.
     * 
     * "normal" = if the user is already signed in with VERTX, 
     * continue silently, otherwise the user will be asked to log in.
     * 
     * "prompt" = the user will always be prompted to either continue with
     * an account, or sign in with another one. STRONGLY RECOMMENDED whenever
     * the user explicitly clicks 'sign in'.
     * 
     * "silent" = automatically attempt to sign in without any user interaction.
     * if the user cannot be silently signed in (e.g. password is required), the user is 
     * silently returned to the app with an error. the app can then prompt the user to sign in.
     */
    mode?: "normal" | "prompt" | "silent"
}

export interface SignOutOptions {
    /**
     * if true, sign the user out of their VERTX account as well as this application.
     * you should usually make it clear to the user that you are doing this.
     * 
     * if false, the user will be signed out of this app only.
     */
    signoutVertex?: boolean
}