import * as Tone from "tone"
import { Pads } from "../main";
import { jsx } from "@vertx/jsx";
import { PatchContainer } from "../views/patch/patchView";
import { PatchItem } from "../views/patch/patchItemView";
import { PadManager } from "./padManager";
import { Pad } from "./pad";

export class MasterBus {

    masterChannel:Tone.Channel
    FFT:Tone.FFT;
    FFTCanvas:HTMLCanvasElement = document.getElementById("master-fft-canvas") as HTMLCanvasElement;

    init() {

        this.masterChannel = new Tone.Channel().toDestination();
		this.FFT = new Tone.FFT(64);
        this.masterChannel.connect(this.FFT);
        
        return this.masterChannel;
    }

}

/** An Audio Bus through which signsals are recieved in and sent out. */
export class Channel {

    toMaster:boolean;
    parentObject: Pad | PadManager;
    parentChannel: Tone.Channel
    channel:Tone.Channel
    color:string

    /** 
     * @param "Parent to connect to"
     * @param "If the channel connects to master. Should be changed."
     */
    constructor(parentObject,toMaster) {
        this.parentObject = parentObject;
        this.toMaster = toMaster
    }

    setVolume(volume:number) {
        this.channel.volume.value = volume;
    }

    setPan(pan:number) {
        this.channel.pan.value = pan;
    }

    init() {

        this.channel = new Tone.Channel(0);
        if (this.toMaster == true) {
            this.connectToParent(Pads.Application.masterChannel)
        }
        else {
            this.connectToParent(this.parentObject.channel);
        }
        return this.channel;

    }

    connectToParent(channel) {

        this.channel.connect(channel);

    }

    setupDefaultUI():HTMLElement {

        if (!this.color) {
            this.color = "#fff";
        }

        // Volume
        let volumeSlider = <input type="range" data-diameter="30" min="-10" max="10" value="0" class="input-knob mr-1" data-bgcolor ={this.color}/> as HTMLInputElement
        volumeSlider.addEventListener('input',(e) => {
            let target = e.target as HTMLInputElement;
            let volumeValue = parseInt(target.value);
            this.setVolume(volumeValue);
        })
        let volumeWrapper = <PatchItem name="Volume">{volumeSlider}</PatchItem>

        // Pan
        let panKnob = <input type="range" data-diameter="30" min="-5" max="5" value="0" step="0.1" class="input-knob mr-1" data-bgcolor ={this.color} /> as HTMLInputElement
        panKnob.addEventListener('input',(e) => {
            let target = e.target as HTMLInputElement;
            let panValue = parseInt(target.value);
            this.setPan(panValue)
        })
        let panWrapper = <PatchItem name="Pan">{panKnob}</PatchItem>

        // Return Container
        let channelMixer = <PatchContainer name="Channel Mixer" color={this.color}>{volumeWrapper}{panWrapper}</PatchContainer> as HTMLElement


        return channelMixer;

    }

}