import * as Tone from "tone"
import { jsx } from "@vertx/jsx";
import { Pads } from "../main";
import { Utils } from "./Utils";
import { PadManager } from "./padManager";
import { Channel } from "./Channel";
import { ContextMenu } from "../views/contextMenu/contextMenu";
import { EffectsStack } from "./effectsStack";
import { Pattern } from "./pattern";

// Manages groups of PadManagers
export class PadGroup {

    constructor(index,parent,buttonParent,color) {
        this.index = index;
        this.parent = parent;
        this.buttonParent = buttonParent;
        this.color = color;
        this.colorHex = Utils.getColorValue(this.color)
    }

    type:"PadGroup";
    index:number;
    parent:HTMLElement

    // Bus
    channel:Tone.Channel
    effectsStack;

    // Patterns
    activePattern:Pattern;
    patterns:Pattern[];

    buttonParent:HTMLElement;
    groupButton:HTMLElement;
    groupContainer:HTMLElement;
    padManager:PadManager;
    color:string;
    colorHex:string;

    contextContainer = document.getElementById('context-container');
    sequenceWrapper:HTMLElement;
    sequenceButtonArea:HTMLElement
    groupContext:HTMLElement

    /** Initialises the Pad group */
    async init() {

        // Patterns
        this.patterns = [];
        this.sequenceWrapper = document.getElementById("sequencer-wrapper");
        this.sequenceButtonArea = document.getElementById("sequence-top-bar");

        // Setup group channel and get the default UI view.
        let channel = new Channel(Pads.Application.masterChannel,true);

        channel.color = this.colorHex;
        let defaultUI = channel.setupDefaultUI();
        this.channel = channel.init()

        // Setup group container for group.
        this.groupContainer = <div class={`group-container`}></div> as HTMLElement
        this.parent.append(this.groupContainer);

        // Setup context container for group.
        this.groupContext = <div class={`pad-context d-none`}></div> as HTMLElement

        // Add channel to context
        this.groupContext.append(defaultUI);
        // Add group to context.
        this.contextContainer.append(this.groupContext);
        
        // Initialise a new pad manager.
        this.padManager = new PadManager(this.groupContainer,this.color,this.channel,this);
        await this.padManager.init();

        // Setup pattern manager.
        if (this.index == 0) {
            Pads.Application.activeGroup = this;
            let pattern = new Pattern(this);
            pattern.init();
            this.patterns.push(pattern);
            Pads.Application.activePattern = pattern;
            this.renderPatternButton(pattern);

            let addPatternButton = <button class={`default-button ${this.color}`}>Add Pattern</button>
            this.sequenceButtonArea.appendChild(addPatternButton);
            addPatternButton.addEventListener('click',() => {
                console.error("Add Pattern");

                let newPattern = new Pattern(this);
                newPattern.init();
                this.padManager.singlePadArray.forEach(pad => {
                    if (pad.isReady) {
                        newPattern.newSequence(pad);
                    }
                })
                this.patterns.push(newPattern);
                this.selectPattern(newPattern);
                this.renderPatternButton(newPattern);
            })

        }


        // Create button for toggling to this group.
        this.groupButton = <button class={`group-button inactive ${this.color}`}>{`G${this.index + 1}`}</button> as HTMLButtonElement;
        this.buttonParent.append(this.groupButton);

        // Context menu for group button (currently does nothing)
        this.groupButton.addEventListener('contextmenu',(e) => {
            e.preventDefault();
            let context = <ContextMenu item={this.padManager} event={e}></ContextMenu>
        })

        // Toggle this groups context when clicking the group button.
        this.groupButton.addEventListener('click',() => {

            let groupButtons = document.querySelectorAll(".group-button");
            groupButtons.forEach(button => {
                button.classList.add('inactive');
            })
            this.groupButton.classList.remove('inactive');
            this.renderContext();
        })

        // Setup the effect stack for this group.
        this.initEffectsStack()

        // Ready
        return true;

    }

    async disposePatterns(ignoreIndex?:number) {
        this.patterns.forEach((pattern,index) => {
            if (index == ignoreIndex) {
                return;
            }
            setTimeout(() => {
                let sequences = pattern.getSequences();
                sequences.forEach(sequence => {
                    sequence.getSequence().then((toneSequence) => {
                        if (toneSequence) {
                            pattern.disposeSequence(sequence);
                        }
                    })
                })
            }, 200);
        })
        return true;
    }

    async selectPattern(pattern:Pattern) {

        let isReady = await this.disposePatterns(this.patterns.indexOf(pattern));
        if (!isReady) {
            return;
        }
        Pads.Application.activePattern = pattern;
        pattern.renderView();

    }

    async renderPatternButton(pattern:Pattern) {

        let addClass = (button:HTMLButtonElement) => {
            let buttons = document.querySelectorAll('.pattern-button');
            buttons.forEach(button => {
                button.classList.add('inactive');
            })
            button.classList.remove('inactive');
        }

        let id = this.patterns.length;
        let button = <button class={`pattern-button default-button ${this.color}`}>{id.toString()}</button> as HTMLButtonElement
        addClass(button);
        this.sequenceButtonArea.prepend(button);
        button.addEventListener('click',async () => {
            this.selectPattern(pattern);
            addClass(button);
        })

    }

    renderContext() {

        let contexts = document.querySelectorAll('.pad-context');
        contexts.forEach(context => {
            context.classList.add('d-none')
        })
        this.groupContext.classList.remove('d-none');

    }

    // Move this to a global function.
    initEffectsStack() {

        // [todo] This is absolutely horrid.
        this.effectsStack = new EffectsStack(this.channel,this.groupContext,Pads.Application.masterChannel,this.color);
        this.effectsStack.render();

    }

    async render() {

        //

    }

}