import toastr from "toastr"

export class Utils {

    // Files

    static async createFile(url) {
        let name = url.substring(url.lastIndexOf('/') + 1);
        let response = await fetch(url);
        let data = await response.blob();
        let file = new File([data], name);
        return file;
    }

    static async getFileFromUrl(url, name, defaultType = 'audio/mp3') {
        const response = await fetch(url);
        const data = await response.blob();
        return new File([data], name, {
            type: data.type || defaultType,
        });
    }

    static async getFileFromUrl2(url, defaultType = 'audio/mp3') {
        const response = await fetch(url);
        const data = await response.blob();
        return new File([data], "test", {
            type: data.type || defaultType,
        });
    }

    static getFileName(string) {
        let name = string.substring(string.lastIndexOf('/') + 1);
        return name;

    }

    // Rendering

    static getCursorPosition(canvas, event) {
        const rect = canvas.getBoundingClientRect()
        const x = event.clientX - rect.left
        return x
    }

    static getScaledValue(value, sourceRangeMin, sourceRangeMax, targetRangeMin, targetRangeMax) {
        var targetRange = targetRangeMax - targetRangeMin;
        var sourceRange = sourceRangeMax - sourceRangeMin;
        return (value - sourceRangeMin) * targetRange / sourceRange + targetRangeMin;
    }

    static getColorValue(color) {
        let colorHex;
        switch(color) {
            // case "primary":
            //     colorHex = '#fffa29'
            case "primary":
                colorHex = "#ffb3b3";
                break;
            case "pink":
                colorHex = '#ffb3b3'
                break;
            case "blue":
                colorHex = '#5684f5'
                break;
            case "green":
                colorHex = '#5bf099'
                break;
        }
        return colorHex;
    }

    

}

export module Notifications {

    export class Error {
        static show(message:string) {
            toastr.error(message)
        }
    }

}