import * as Tone from "tone"
import { Pads } from "../main"
import { jsx } from "@vertx/jsx";
import { PadGroup } from "./padGroup";
import { Pad } from "./pad";
import { Pattern } from "./pattern";
import { v4 as uuidv4 } from 'uuid';


export class Sequence {

    constructor(pattern:Pattern,pad:Pad) { 
        this.pattern = pattern;
        this.pad = pad;
    }

    id:string;
    name:string;
    masterPlay:HTMLButtonElement;
    masterPause:HTMLButtonElement;
    wrapper:HTMLElement;
    padGroup:PadGroup;
    pad:Pad;
    loop:Tone.Loop;
    speed:string;
    count:number; // The point along the sequence. This will probably have to change later.
    rendered:boolean; // Has been rendered flag.
    hasStarted:boolean;

    //player:Tone.Player;

    // The Sequence
    sequenceLength:number;
    sequenceArray:string[];
    sequence:Tone.Sequence;
    sequenceButtons:HTMLElement[];
    sequencerGrid:HTMLElement;

    // [todo]
    pattern:Pattern; // The pattern this sequence will belong to.


    async getSequence() {

        console.error("ID",this.id);
        return this.sequence;

    }

    triggerPlayer(time:number,note:string) {

        if (this.sequence == null) {
            return;
        }
        if (!this.pad.activeComponent) {
            console.log("No active component");
            return;
        }
        if (note !== "") {
            this.pad.activeComponent.play(time);
        }
    }
    // triggerAnimation(time) {
    //     let self = this;
    //     Tone.Draw.schedule(function(){
    //         //this callback is invoked from a requestAnimationFrame
    //         //and will be invoked close to AudioContext time
    //         self.sequenceButtons.forEach(button => {
    //             button.classList.remove('sequence-active');
    //         })
    //         console.log("Btn",self.sequenceButtons);
    //         console.log("Count",Pads.Application.count);
    //         self.sequenceButtons[Pads.Application.count].classList.add('sequence-active');

    //     }, time) //use AudioContext time of the event
    // }

    async disposeSequence() {

        console.error("Dispose..")
        this.sequence.stop();
        this.sequence.clear();
        this.sequence.dispose();
        this.sequence = null;
        return true;

    }

    async initSequence() {

        console.error("Init ID",this.id);
        console.error("Init Sequence")
        if (this.sequence) {
            await this.disposeSequence();
            //return;
        }
        this.sequence = new Tone.Sequence((time, note) => {
            // [removed]
            //this.triggerPlayer(this.player,time,note);
            this.triggerPlayer(time,note);

        }, this.sequenceArray).start(0);
        this.hasStarted = true;

    }

    updateSequence(index:number,value:string) {

        this.sequenceArray.splice(index,1,value);
        this.initSequence();

        if (value == "loaded") {
            this.sequenceButtons[index].classList.add('loaded');
        }
        else {
            this.sequenceButtons[index].classList.remove('loaded');
        }

    }

    renderView() {

        console.log("Name..",this.name);
        console.log("Render View..",this.id);
        this.sequenceButtons = [];

        // If the grid doesn't exist, create it.. this is so we can re-render.
        this.sequencerGrid = <div class="sequencer-grid"></div> as HTMLElement;
        this.wrapper.append(this.sequencerGrid);

        // Clear it incase it has been rendered.
        //this.sequencerGrid.innerHTML = "";

        // Debug length setter.
        let lengthInput = <input type="number" min="1" max="16" value={this.sequenceLength}></input> as HTMLInputElement
        this.sequencerGrid.prepend(lengthInput);
        lengthInput.addEventListener('change',() => {
            this.setLength(lengthInput.value);
        })
        
        let name = <h3 style="width:100px;overflow:hidden">{this.name}</h3>
        this.sequencerGrid.prepend(name);
        console.log(this.sequenceLength);
        let note = 1;
        for (let p=0;p<this.sequenceLength;p++) {
            let button = <button class={`sequencer-pad ${this.pad.color}`} data-loaded={false}>{note.toString()}</button> as HTMLButtonElement;
            note++
            this.sequenceButtons.push(button);
            if (this.sequenceArray[p] == "loaded") {
                button.classList.add('loaded');
            }
            this.sequencerGrid.append(button);
            button.addEventListener('click',() => {
                if (this.sequenceArray[p] == "loaded") {
                    this.updateSequence(p,"")
                }
                else {
                    this.updateSequence(p,"loaded")
                }
            })
 
        }
        // Don't repeatedly set up the loop.
        if (this.rendered)
            return;
        // if (this.loop) {
        //     this.loop.dispose();
        //     this.loop = null;
        // }
        // Get animation loop ready when the pads are first rendered.
        // let length = (this.sequenceLength / 2) + "n"
        // console.log(length);
        this.rendered = true;
    }

    init() {


        console.error("Init Sequence Proper");
        this.name = this.pad.name;
        this.hasStarted = false;
        this.id = uuidv4();
        //this.pad = this.pattern.pad;
        //this.count = Pads.Application.count;
        this.rendered = false;
        this.sequenceButtons = [];
        this.wrapper = document.getElementById('sequence-inner') as HTMLElement;

        this.masterPlay = document.getElementById("master-play") as HTMLButtonElement
        this.masterPause = document.getElementById("master-pause") as HTMLButtonElement

        this.sequenceArray = [];
        this.sequenceLength = 16;

        for (let p=1;p<=this.sequenceLength;p++) {
            this.sequenceArray.push("");
        } 
        //this.renderView();
		// connect the UI with the components
		// this.masterPlay.addEventListener("click", () => this.startLoop());
		// this.masterPause.addEventListener("click", () => this.stopLoop());

    }

    // Set a new length for the current sequence and re-render.
    // There is a chance this is hard-coded somewhere else, and possible in pattern, so check.
    setLength(length) {

        let currentLength = this.sequenceArray.length;
        this.sequenceLength = length;

        // Nothing has changed.
        if (currentLength == length)
            return;

        // Adjust sequence array
        if (currentLength < length) {
            let difference = length - currentLength;
            for (let i=0; i<difference;i++) {
                this.sequenceArray.push("");
            }
        }
        if (currentLength > length) {
            let difference = currentLength - length;
            this.sequenceArray.splice(length,difference);
        }
        // Re-render
        //this.sequencerGrid.remove();
        this.pattern.renderView();

        // this.sequencerGrid.remove()
        // this.renderView();
        // this.initSequence();
    }

    async startLoop() {
        Pads.Application.count = 0;
        let length = this.sequenceLength;
        let time = (this.sequenceLength / 2) + "n";
        Pads.Application.triggerCounter(length,time);
        this.initSequence();
        await Tone.start();
        // Buffer improves accuracy
        //Tone.Transport.start("+2.0")
        Tone.Transport.start();

    }
    stopLoop() {
        this.disposeSequence();
        Tone.Transport.stop()
        Pads.Application.endCounter();
        setTimeout(() => {
            this.sequenceButtons.forEach(button => {
                button.classList.remove('sequence-active');
            })
        }, 100);
    }

}