import { jsx, JSXComponent, JSXElement } from "@vertx/jsx";
import { Utils } from "../../components/Utils";
import { Pads } from "../../main"

export class PatchContainer implements JSXComponent {

    name:string | undefined;
    color:string | undefined;
    delete: () => void = () => {}

    render(children:JSXElement[]):JSXElement {

        if (!this.color) {
            this.color = Utils.getColorValue("primary");
        }

        let panel = <div class="patch-panel">
                        <div class={`patch-top-bar`} style={`background:${this.color}`}><h3 class="title">{this.name}</h3></div>
                        <div class="d-flex" style="margin:10px;">
                            <div class="d-flex row">
                                {children}
                            </div>
                        </div>
                    </div>

        return panel
    }
}

export class ComponentContainer implements JSXComponent {

    name:string | undefined;
    color:string | undefined;
    delete: () => void = () => {}

    render(children:JSXElement[]):JSXElement {

        if (!this.color) {
            this.color = Utils.getColorValue("primary");
        }

        let panel = <div class="patch-panel d-block">
                        <div class={`patch-top-bar`} style={`background:${this.color}`}><h3 class="title">{this.name}</h3></div>
                        <div style="padding:10px;">
                            {children}
                        </div>
                    </div>

        return panel
    }
}