import { Sequence } from "./sequence";
import { v4 as uuidv4 } from 'uuid';
import { Pad } from "./pad";
import { PadGroup } from "./padGroup";



// Keep it simple for now.
// A pattern just plays all it's sequences, now.

// interface PatternObject {

//     id:string,
//     active:boolean, // May not need.
//     pattern:Pattern;

// }


export type tLength = 8 | 16 | 32 | 64 // Lengths of patterns for now.

export class Pattern {

    constructor(group:PadGroup) {
        this.tag = "pattern"
        this.id = uuidv4();
        this.length = 16;
        this.group = group;
    }

    // I own sequences.
    id:string;
    tag:string;

    pad:Pad;
    group:PadGroup;
    length:tLength;
    sequences:Sequence[];

    // DOM
    masterPlay:HTMLButtonElement;
    masterPause:HTMLButtonElement;
    color:string;
    sequenceInner = document.getElementById('sequence-inner') as HTMLElement;


    init() {

        this.sequences = [];
        this.color = this.group.color;
       // this.newSequence(Pads.Application.activePad);
        this.renderView();
        return true;
        
    }


    play() {

    }

    stop() {


    }

    setLength(length:tLength) {

        this.length = length;

    }

    newSequence(pad:Pad) {
        console.error("New Sequence");
        let pattern = this;
        let sequence = new Sequence(pattern,pad);
        pad.sequencer = sequence;
        sequence.init();
        this.sequences.push(sequence);
        console.warn("[ PADS ] New Sequence.")

    }

    getSequences () {

        return this.sequences;

    }

    renderView() {

        this.sequenceInner.innerHTML = "";
        console.error("Render View");

        this.sequences.forEach(sequence => {
            console.log("Init",sequence.id);
            sequence.initSequence();
            sequence.pad.sequencer = sequence;
            sequence.renderView();
        })

    }

    disposeSequence(sequence:Sequence) {

        console.error("Sequence..",sequence);
        if (sequence.sequence) {
            sequence.sequence.stop();
            sequence.sequence.dispose();
            sequence.sequence = null;
        }

    }

}