import * as Tone from "tone"
import { jsx } from "@vertx/jsx";
import { Pads } from "../main";
import { Utils } from "./Utils";
import { Channel } from "./Channel";
import { ContextMenu } from "../views/contextMenu/contextMenu";
import { EffectsStack } from "./effectsStack";
import { Pad, PadComponent } from "./pad";
import { SamplePad } from "./sampler";
import { PadGroup } from "./padGroup";

export type PadMode = "Single" | "PianoRoll";
let PadModes:PadMode[] = ["Single","PianoRoll"]

// Managers an array of pads.
export class PadManager {

    type:string = "PadManager"
    parent:HTMLElement;
    singlePadContainer:HTMLElement = <div class="pad-array standard-sort"></div> as HTMLElement
    singlePadArray:Pad[] = [];
    pianoPadContainer:HTMLElement = <div class="pad-array reverse-sort" style="display:none"></div> as HTMLElement

    channel:Tone.Channel // Passed from PadGroup.

    pianoPadArray:Pad[] = [];
    activePad:Pad = null;
    lastPad:Pad = null;
    mode:PadMode = "Single"
    keyList = ["KeyA","KeyS","KeyD","KeyF","KeyG","KeyH","KeyJ","KeyK","KeyZ","KeyX","KeyC","KeyV","KeyB","KeyN","KeyM","Comma"]
    renderedSingles:number = 0;
    color:string;
    padGroup:PadGroup; // Parent

    constructor(parent,color,channel,padGroup) {

        this.parent = parent;
        this.color = color;
        this.channel = channel;
        this.padGroup = padGroup;
    }

    async init() {
        
        this.parent.prepend(this.pianoPadContainer);
        this.parent.prepend(this.singlePadContainer);
        this.renderMode("Single");
        PadModes.forEach(mode => {
            let modeButton = <button id={`${mode}-mode-toggle`} class={`default-button ${this.color} mode-toggle inactive d-none`}>{mode}</button> as HTMLButtonElement
            modeButton.addEventListener('click',() => {
                let toggles = document.querySelectorAll('.mode-toggle');
                toggles.forEach(toggle => {
                    toggle.classList.add('inactive');
                })

                modeButton.classList.remove('inactive');
                this.mode = mode;
                this.renderMode(this.mode);
            })
            this.parent.prepend(modeButton);
        })
        // html stuff : (
        let elemToFind = 'Single-mode-toggle';
        let toggle = document.getElementById(elemToFind);
        toggle.classList.remove('inactive');
        let length = 16;
        for (let i=0;i<length;i++) {
            let pad = this.addPad(i,'unloaded',"Sampler");

        }

        return true;
    }

    addPad(index,name,component:PadComponent) {

        let pad = new Pad(index,component,this.singlePadContainer,name,this.channel,this.color,this);
        pad.init(); 
        this.singlePadArray.push(pad);
        this.bindPad(pad);
        return pad;

    }

    bindKeys() {

        this.singlePadArray.forEach((pad,index) => {
            pad.HTMLElement.dataset.key = this.keyList[index];

        })

        this.singlePadArray.forEach((pad,index) => {

            document.addEventListener('keypress',(e) => {
                if (e.code == this.keyList[index]) {
                    Pads.Application.toggleRightContent("Context");
                    let pad = Pads.Application.activeGroup.padManager.singlePadArray[index];
                    Pads.Application.activePad = pad;
                    this.render(pad);
                    if (!Pads.Application.activePad.activeComponent) {
                        Pads.Application.toggleRightContent("Explorer");
                    }
                    else {
                        Pads.Application.toggleRightContent("Sequencer");
                    }
                    pad.renderContext();                    
                    if (!pad.activeComponent) return;
                    if (!pad.activeComponent.isReady) return;
                    if (pad.isPlaying) return;
                    pad.play()

                }
            })

            document.addEventListener('keyup',(e) => {
                if (e.code == this.keyList[index]) {
                    if (!pad.isPlaying)
                        return;
                    pad.stop()
                }
            })

        })
    }

    unbindKeys() {

    }
    
    bindPad(pad:Pad) {
        
        pad.HTMLElement.onmousedown = () => {
            Pads.Application.activePad = pad;
            console.log("Active Pad:",Pads.Application.activePad);
            this.activePad = pad;
            this.render(pad);
            pad.renderContext();
            
            if (!Pads.Application.activePad.activeComponent) {
                Pads.Application.toggleRightContent("Explorer");
            }
            else {
                Pads.Application.toggleRightContent("Context");
            }
            
            if (pad.activeComponent) {
                if (pad.activeComponent.isReady) {
                    pad.play();
                }
            }
            return;
        }
        pad.HTMLElement.onmouseup = () => {
            if (pad.activeComponent) {
                if (pad.activeComponent.isReady) {
                    pad.stop();
                }
            }
        }
        this.renderedSingles++
        if (this.renderedSingles == 16) {
            this.bindKeys()
        }
    }


    render(pad:Pad) {

        // Html stuff.. :(
        let pads = document.querySelectorAll('.pad');
        pads.forEach(pad => {
            pad.classList.remove('selected');
        })
        pad.HTMLElement.classList.add('selected');

    }
    
    renderMode(mode:PadMode) {

        if (mode == "Single") {
            this.singlePadContainer.style.display = "flex"
            this.pianoPadContainer.style.display = "none";
        }

        if (mode == "PianoRoll") {
            if (!this.activePad) {
                window.alert('Please select a Pad.')
                return;
            }
            if (!this.activePad.activeComponent.isReady) {
                window.alert("Please Load a patch");
                return;
            }

            this.singlePadContainer.style.display = "none"
            this.pianoPadContainer.style.display = "flex";

    
            if (this.activePad) {
                let length = 16;
                this.pianoPadContainer.innerHTML = "";

                let clone = this.activePad.activeComponent.getContext() as SamplePad;                
                let pitchShift = new Tone.PitchShift().toDestination()

                let file = clone.currentFile;
                let audio = URL.createObjectURL(file); 
        
                let newPlayer = new Tone.Player({
                    url:audio,
                    loop:true
                })
        
                Tone.loaded().then((tone) => {
                    newPlayer.connect(pitchShift);
                    newPlayer.loopStart = clone.loopStart;
                })

                let pitch = 3 // down one octave.
                
                for (let i=0;i<length;i++) {

                    let thisPitch = pitch;
                    let padTitle = 'Pitch ' + thisPitch.toString();

                    let pianoPad = <button class="pad">{padTitle}</button> as HTMLButtonElement
                    this.pianoPadContainer.append(pianoPad);
                    pianoPad.onmousedown = () => {
                        //player.start();
                        pitchShift.pitch = thisPitch;
                        newPlayer.start()
                        //player.start();
                    }
                    pianoPad.onmouseup = () => {
                        //player.stop();
                        //player.stop();
                        newPlayer.stop();
                    }

                    pitch--

                }
            }

        }

    }

}