import { jsx, JSXComponent, JSXElement } from "@vertx/jsx";

export class EffectsStackModule implements JSXComponent {

    name:string | undefined;

    render(children:JSXElement[]):JSXElement {

        let effectModule = <div class="d-flex w-100 border-bottom">
                                <div class="d-flex flex-1" style="padding:10px">
                                    <div class="name-area d-flex flex-1 align-center">
                                        <p class="text-align-center">{this.name}</p>
                                    </div>
                                    <div class="module-effects-area">
                                        {children}
                                    </div>
                                </div>
                        </div> as HTMLElement

        return effectModule;
    }
}

