import { jsx, JSXComponent, JSXElement } from "@vertx/jsx";
import { Utils } from "../../components/Utils";
import * as Tone from "tone"
import { PatchItem } from "./patchItemView";
import { Param } from "tone";
import { EffectsStackModule } from "./effectStackModuleView";

export type Effect = "Reverb" | "BitCrush" | "PitchShift"
let Effects:Effect[] = ["Reverb","BitCrush","PitchShift"]

export interface StackObject {

    name:Effect,
    node:Tone.InputNode,
    HTMLItem:HTMLElement;

}

export class EffectsStackView implements JSXComponent {

    // Nodes
    channel:Tone.Channel
    destination:Tone.Channel;
    stack:StackObject[] = [];

    // Effects
    reverb:Tone.Reverb;
    bitCrush:Tone.BitCrusher;
    pitchShift:Tone.PitchShift;

    // Elements etc.
    name:string | undefined;
    color:string | undefined;
    context:HTMLElement
    effectsList:HTMLElement;


    delete: () => void = () => {}

    toggleEffect(e:MouseEvent,effect:Effect) {
        let button = e.target as HTMLButtonElement;
        let isActive = false;
        for (let node of this.stack.entries()) {
            let index = node[0];
            if (node[1].name == effect) {
                isActive = true;
                node[1].HTMLItem.remove();
                this.stack.splice(index);
            }
        }
        if (isActive) {
            this.disconnect(effect);
            this.chain();
            isActive = false;
            button.classList.add('inactive');
        }
        else {
            this.add(effect);
            button.classList.remove('inactive')
        }


    }

    add(effect:Effect) {

        switch(effect) {
            case "Reverb":
                this.initReverb();
                break;
            case "BitCrush":
                this.initBitCrush();
                break;
            case "PitchShift":
                this.initPitchShift();

        }
        // [todo]
        this.chain();

    }

    initPitchShift() {

        // View.. These inputs need to be moved into their own component.
        let pitch = <input type="range" data-diameter="30" min="-24" max="24" value="0" step="0.1" class="input-knob" data-bgcolor ={this.color} /> as HTMLInputElement
        pitch.addEventListener('change',() => {
            this.pitchShift.pitch = parseInt(pitch.value);
        })
        let pitchShiftView = <div>{pitch}</div>

        let ps = <EffectsStackModule name="Pitch Shift"><PatchItem name="Pitch">{pitchShiftView}</PatchItem></EffectsStackModule> as HTMLElement

        this.effectsList.append(ps);

        // Reverb Actual
        this.pitchShift = new Tone.PitchShift(0);
        Tone.connect(this.channel,this.pitchShift);
        let shift:StackObject = {
            name:"PitchShift",
            node:this.pitchShift,
            HTMLItem:ps
        }
        this.stack.push(shift);

    }

    initReverb() {

        // View.. These inputs need to be moved into their own component.
        let reverbDecay = <input type="range" data-diameter="30" min="1" max="20" value="1" class="input-knob" data-bgcolor ={this.color} /> as HTMLInputElement
        reverbDecay.addEventListener('change',() => {
            this.reverb.decay = reverbDecay.value;
        })
        let reverbView = <div>{reverbDecay}</div>

        let r = <EffectsStackModule name="Reverb"><PatchItem name="Decay">{reverbView}</PatchItem></EffectsStackModule> as HTMLElement

        this.effectsList.append(r);

        // Reverb Actual
        this.reverb = new Tone.Reverb(0.1)
        Tone.connect(this.channel,this.reverb);
        let reverb:StackObject = {
            name:"Reverb",
            node:this.reverb,
            HTMLItem:r
        }
        this.stack.push(reverb);

    }
    initBitCrush() {

        // View.. These inputs need to be moved into their own component.
        let crusherBits = <input type="range" data-diameter="30" min="1" max="8" value="1" class="input-knob" data-bgcolor ={this.color} /> as HTMLInputElement;
        crusherBits.addEventListener('change',() => {

            this.bitCrush.set({
                bits:parseInt(crusherBits.value)
            })
        })
        let crusherView = <div>{crusherBits}</div>
        let cr = <EffectsStackModule name="BitCrusher"><PatchItem name="Bits">{crusherView}</PatchItem></EffectsStackModule> as HTMLElement;

        this.effectsList.append(cr);

        // Bitcrusher actual
        this.bitCrush = new Tone.BitCrusher(1);
        Tone.connect(this.channel,this.bitCrush);
        let bitCrush:StackObject = {
            name:"BitCrush",
            node:this.bitCrush,
            HTMLItem:cr
        }
        this.stack.push(bitCrush);

    }

    chain() {

        let nodesInStack = [];
        let nodes = this.stack.entries();
        for (let node of nodes) {
            if (node) {
                nodesInStack.push(node[1].node);
            }
        }
        this.channel.connect(this.destination);
        this.channel.disconnect(this.destination);
        this.channel.chain(...nodesInStack,this.destination);

    }

    disconnect(effect:any) {

        Tone.disconnect(this.channel,effect);

    }

    solo() {

        // [todo]

    }

    setup() {

        let effectButtons = [];

        Effects.forEach(effect => {

            let button = <button class="effect-button inactive" style={`background:${this.color}`} onclick={(e) => {this.toggleEffect(e,effect)}}>{effect}</button>
            effectButtons.push(button);

        })

        return <div class="effects-button-wrapper" style={`background:${this.color}40`}>{effectButtons}</div>

    }

    render(children:JSXElement[]):JSXElement {

        let buttons = this.setup();

        if (!this.color) {
            this.color = Utils.getColorValue("primary");
        }

        this.effectsList = <div class="d-flex column effects-item-list"></div> as HTMLElement
        let panel = <div class="patch-panel">
                        <div class={`patch-top-bar`} style={`background:${this.color}`}><h3 class="title">{this.name}</h3></div>
                        <div class="d-flex effects-view-container">
                            {this.effectsList}
                            <div class="effects-button-list">
                                {buttons}
                            </div>
                        </div>

                    </div>

        return panel
    }
}