import { jsx } from "@vertx/jsx";
import { Pads } from "../main";
import { PadGroup } from "./padGroup";

// Manages groups of PadGroups.
export class GroupManager {

    parent:HTMLElement;
    buttonContainer:HTMLElement;
    activeGroup:PadGroup;
    padGroups:PadGroup[] = [];
    groupLimit:number = 4;
    padColors:string[] = Pads.Application.config.PadColors;
 
    constructor(parent) {
        this.parent = parent;
    }

    async init() {

        // Setup groups
        this.buttonContainer = <div class="button-group-group"></div> as HTMLElement;
        this.parent.append(this.buttonContainer);

        for (let i=0;i<this.groupLimit;i++) {
        
            // Create group items
            let group = new PadGroup(i,this.parent,this.buttonContainer,this.padColors[i]);
            await group.init();
            group.groupButton.onclick = () => {
                this.selectGroup(group);
            }
            this.padGroups.push(group);
        }
        Pads.Application.activeGroup = this.padGroups[0];
        let groupButton = document.querySelector('.group-button');
        groupButton.classList.remove('inactive');
        this.bindKeys();
        return true;

    }

    selectGroup(group:PadGroup) {

        Pads.Application.activeGroup = group;
        this.render(group);
        if (Pads.Application.activeGroup.padManager.activePad) {
            Pads.Application.activeGroup.padManager.activePad.padContext.classList.add('d-none');
            Pads.Application.activeGroup.padManager.activePad = null;
        }


    }

    bindKeys() {

        let keys = ["Digit1","Digit2","Digit3","Digit4"]

        this.padGroups.forEach((group,index) => {

            document.addEventListener('keypress',(e) => {
                if (e.code == keys[index]) {

                    let groupButtons = document.querySelectorAll(".group-button");
                    groupButtons.forEach(button => {
                        button.classList.add('inactive');
                    })
                    Pads.Application.activeGroup = group;
                    group.groupButton.classList.remove('inactive');
                    this.selectGroup(group);
                    Pads.Application.activeGroup.renderContext();

                }
            })

        })
    }


    render(group:PadGroup) {

        // Hide all groups
        this.padGroups.forEach(group => {
            group.groupContainer.classList.add('d-none');
        })
        group.groupContainer.classList.remove('d-none');

    }

}